@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes slideUp {
  0% {
    opacity: 0;
    transform: translate3d(0, 100%, 0);
  }
  100% {
    opacity: 1;
    transform: none;
  }
}
@keyframes slideDown {
  0% {
    opacity: 0;
    transform: translate3d(0, -100%, 0);
  }
  100% {
    opacity: 1;
    transform: none;
  }
}
.projects {
  padding-bottom: 60px;
  box-sizing: border-box;
  min-height: 100%;
  background: #6f2525;
}
.projects .wall {
  background: #a03535;
  min-height: 80%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-end;
}
.projects .wall .container {
  width: 100%;
  margin-bottom: 40px;
}
.projects .wall .container h1 {
  color: #491818;
  margin-top: 15px;
  font-size: 60px;
  font-weight: 100;
  text-align: center;
  font-family: 'Great Vibes', cursive;
  animation-name: slideDown;
  animation-duration: 0.2s;
}
.projects .wall .artworks {
  width: 60%;
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-end;
  animation-name: slideDown;
  animation-duration: 0.2s;
}
.projects .wall .artworks a {
  padding: 20px;
  text-decoration: none;
  text-align: center;
  display: block;
  position: relative;
}
.projects .wall .artworks a svg {
  display: block;
  margin: auto;
  margin-bottom: 10px;
}
.projects .wall .artworks a h3 {
  color: #5c1f1f;
  font-family: "Open Sans";
  font-weight: 800;
  font-size: 15px;
  padding-bottom: 3px;
  text-transform: uppercase;
  display: inline;
}
.projects .wall .artworks a p {
  position: absolute;
  display: none;
  background-color: hsla(0, 0%, 20%, 0.9);
  color: white;
  font-family: 'Source Sans Pro';
  font-weight: 300;
  font-size: 15px;
  border-radius: 3px;
  padding: 10px;
  margin-left: -14px;
  z-index: 1;
}
.projects .wall .artworks a p::before {
  position: absolute;
  bottom: 100%;
  left: 50%;
  width: 0;
  border-bottom: 5px solid #000;
  border-bottom: 5px solid hsla(0, 0%, 20%, 0.9);
  border-right: 5px solid transparent;
  border-left: 5px solid transparent;
  content: " ";
  font-size: 0;
  line-height: 0;
}
.projects .wall .artworks a:hover h3 {
  border-bottom: 1px solid #6f2525;
}
.projects .wall .artworks a:hover p {
  display: block;
  animation-name: fadeIn;
  animation-duration: 0.2s;
}
.projects .wall > svg {
  margin-top: -50px;
  margin-right: 2px;
  margin-left: 2px;
  margin-bottom: -20px;
  animation-name: slideUp;
  animation-duration: 0.2s;
}
@media (max-width: 799px) {
  .mobileHidden {
    display: none;
  }
  .projects .wall .artworks {
    width: 100%;
  }
  .projects .wall .artworks a {
    width: 100%;
  }
  .projects .wall .artworks a p {
    position: relative;
    display: block;
    margin: auto;
    margin-top: 15px;
    opacity: 1;
    max-width: 200px;
  }
  .projects .wall .artworks a p.mobileHidden {
    display: none;
  }
  .projects .wall .artworks a:hover p {
    animation-duration: 0s;
  }
  .projects .wall > svg {
    display: none;
  }
}
