.bio {
  background: #2980b9;
  min-height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
}
.bio .cloud1,
.bio .cloud2,
.bio .cloud3,
.bio .cloud4,
.bio .cloud5,
.bio .cloud6,
.bio .cloud7,
.bio .cloud8,
.bio .cloud9,
.bio .cloud10,
.bio .cloud11,
.bio .cloud12 {
  position: absolute;
  pointer-events: none;
}
.bio .grass {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 120px;
  pointer-events: none;
}
@media (max-width: 768px) {
  .bio .grass {
    height: 80px;
  }
}
