.menu a {
  display: block;
  width: 250px;
  height: 250px;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  text-decoration: none;
  color: white;
  font-family: 'Open Sans';
  font-size: 20px;
  padding: 5px;
  font-weight: 800;
  text-transform: uppercase;
  transition: width 0.2s, height 0.2s;
  z-index: 10;
}
.menu a:hover {
  width: 300px;
  height: 300px;
}
.menu a.active {
  display: none;
}
.menu .contact {
  background: #111;
  transform: translate(-50%, -50%) rotate(-45deg);
}
.menu .contact:hover {
  background: #444;
}
.menu .bio {
  background: #2980b9;
  top: auto;
  bottom: 0;
  align-items: flex-start;
  transform: translate(-50%, 50%) rotate(45deg);
}
.menu .bio:hover {
  background: #3498db;
}
.menu .blog {
  background: #27ae60;
  left: auto;
  right: 0;
  transform: translate(50%, -50%) rotate(45deg);
}
.menu .blog:hover {
  background: #2ecc71;
}
.menu .projects {
  background: #a03535;
  top: auto;
  left: auto;
  right: 0;
  bottom: 0;
  align-items: flex-start;
  transform: translate(50%, 50%) rotate(-45deg);
}
.menu .projects:hover {
  background: #b33b3b;
}
.menu.bio a {
  background: #20638f;
}
.menu.bio a:hover {
  background: #2472a4;
}
.menu.projects a {
  background: #7a2828;
}
.menu.projects a:hover {
  background: #8d2f2f;
}
.menu.blog a {
  background: #1e8449;
}
.menu.blog a:hover {
  background: #229955;
}
@media (max-width: 599px) {
  .menu a {
    width: 120px;
    height: 120px;
    font-size: 12px;
  }
  .menu a:hover {
    width: 170px;
    height: 170px;
  }
}
